/* Updated Base Styles */
*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  line-height: 1.6;
  -webkit-font-smoothing: antialiased;
  background: linear-gradient(135deg, #83a4d4, #b6fbff);
  min-height: 100vh;
  color: #2d3436;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  padding: 2rem 1rem;
  transition: background 0.5s ease;
}

.weather-container {
  width: 100%;
  max-width: 800px;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(10px);
  margin-top: 1rem;
  overflow: visible;
}

/* Enhanced Form Styling */
form {
  padding: 2rem;
  border-bottom: 1px solid rgba(223, 230, 233, 0.5);
}

.form-header {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 1rem;
}

.form-header i {
  color: #4a90e2;
  font-size: 1.4rem;
}

form label {
  font-size: 1.4rem;
  color: #2d3436;
  font-weight: 600;
}

input[type='text'] {
  width: 100%;
  border: 2px solid #dfe6e9;
  font-size: 1.1rem;
  padding: 1rem;
  border-radius: 12px;
  margin-bottom: 1rem;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

input[type='text']:focus {
  outline: none;
  border-color: #4a90e2;
  box-shadow: 0 0 0 4px rgba(74, 144, 226, 0.1);
}

/* Modern Button Styles */
.button-group {
  display: flex;
  gap: 1rem;
  margin-top: 1rem;
}

button {
  cursor: pointer;
  border: none;
  padding: 1rem 2rem;
  border-radius: 12px;
  font-size: 1rem;
  font-weight: 600;
  transition: all 0.2s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
}

#search {
  background: linear-gradient(135deg, #4a90e2, #3066be);
  color: white;
  flex: 1;
}

#search:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.1);
}

#toggle {
  background: #ffffff;
  border: 2px solid #dfe6e9;
  color: #2d3436;
}

#toggle:hover {
  background: #f8f9fa;
}

/* Weather Data Grid */
#info {
  display: none;
  flex-direction: column;
  gap: 1.5rem;
  padding: 2rem;
}

.location-info {
  text-align: center;
  margin-bottom: 1.5rem;
}

#location {
  font-size: 2rem;
  margin-bottom: 0.5rem;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}

#time {
  font-size: 1.2rem;
  color: #666;
}

.weather-data {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
  gap: 1.5rem;
}

.weather-card {
  background: white;
  padding: 1.5rem;
  border-radius: 16px;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
  transition: transform 0.2s ease;
}

.weather-card:hover {
  transform: translateY(-5px);
}

.weather-card i {
  font-size: 2rem;
  color: #4a90e2;
  margin-bottom: 1rem;
}

.main-temp {
  grid-column: span 2;
  background: linear-gradient(135deg, #4a90e2, #3066be);
  color: white;
}

.main-temp i,
.main-temp .card-label {
  color: rgba(255, 255, 255, 0.9);
}

.card-label {
  font-size: 0.9rem;
  color: #666;
  margin-top: 0.5rem;
}

/* Loading Animation */
#loading {
  display: none;
  flex-direction: column;
  align-items: center;
  padding: 3rem;
}

.loader {
  width: 50px;
  height: 50px;
  border: 4px solid #f3f3f3;
  border-top: 4px solid #4a90e2;
  border-radius: 50%;
  animation: spin 1s linear infinite;
  margin-bottom: 1.5rem;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* Error Message Styling */
#error {
  display: none;
  background: #ffe3e3;
  color: #ff6b6b;
  padding: 1rem;
  border-radius: 8px;
  margin-top: 1rem;
  align-items: center;
  gap: 0.5rem;
}

/* Responsive Design */
@media (max-width: 768px) {
  .weather-container {
    border-radius: 16px;
    margin-top: 0.5rem;
  }

  #location {
    font-size: 1.5rem;
  }

  .weather-data {
    grid-template-columns: 1fr;
  }

  .main-temp {
    grid-column: span 1;
  }

  button {
    padding: 0.75rem 1.5rem;
  }
}

